/*
Webpack - Production
--------------------------------------------------------------------------------

Creating separate webpack configurations for the dev and production environments
--------------------------------------------------------------------------------
1. Install webpack-merge
npm install --save-dev webpack-merge

2. Create webpack.common.js, webpack.dev.js and webpack.prod.js

3. Create NPM scripts in the package.json

"start": "webpack serve --open --config webpack.dev.js",
"build": "webpack --config webpack.prod.js"




Tree shaking is a term commonly used in the JavaScript context for dead-code elimination.

1. In this example, math.js exports two functions (square and cube), however index.js only
imports the cube method.

2. Build
npm run build

3. Look at the output bundle in /dist main.js line 19 and notice that square is not being
imported, however, it is still included in the bundle.





1. Create a package.json:
npm init -y

2. Install webpack packages
npm install webpack webpack-cli --save-dev

3. Create a ./dist directory
This distribution code is the minimized and optimized output of the build process.

4. Installing dependencies
npm install --save lodash
npm install --save-dev lodash

5. Compile and build
npx webpack

The build version is now in ./dist and can be opened in the browser

6. Create a webpack.config.js configuration file in root

7. Compile and build using the config file
npx webpack --config webpack.config.js

8. Setup a shortcut to run wepback builds in the package.json

"scripts": {
    "build": "webpack"
  }

9. Compile and build using the shortcut
npm run build


*/

import { cube } from './math.js';

if (process.env.NODE_ENV !== 'production') {
  console.log('Looks like we are in development mode!');
}

function component() {
  const element = document.createElement('pre');

  element.innerHTML = [
    'Hello webpack!',
    '5 cubed is equal to ' + cube(5)
  ].join('\n\n');

  return element;
}

document.body.appendChild(component());